import React from 'react';
import { Twitter, Instagram, Youtube, Linkedin } from "lucide-react";

// No seu projeto, você pode nomear a função como desejar.
// Ex: export function Footer() { ... }
export default function footerzinho() {
    const socialLinks = [
        { href: "#", icon: Twitter, name: "Twitter" },
        { href: "#", icon: Instagram, name: "Instagram" },
        { href: "#", icon: Youtube, name: "YouTube" },
        { href: "#", icon: Linkedin, name: "LinkedIn" },
    ];

    return (
        <footer className="bg-slate-900 border-t border-slate-800">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8">
                {/* Seção principal com logo e links essenciais */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-8 py-16">
                    
                    {/* Coluna 1: Logo e Descrição */}
                    <div className="col-span-1">
                        <a href="/" className="flex items-center space-x-3">
                            {/* Assumindo que a imagem está na pasta /public */}
                            <img 
                                src="/horde.png" 
                                className="h-10 w-auto" 
                                alt="Logo foodflow"
                                onError={(e) => { e.target.onerror = null; e.target.src='https://placehold.co/40x40/ffffff/16a34a?text=FF'; }}
                            />
                            <span className="text-xl font-bold text-white">
                                foodflow
                            </span>
                        </a>
                        <p className="mt-4 text-sm text-gray-400">
                           Otimizando seu estoque, reduzindo o desperdício.
                        </p>
                    </div>

                    {/* Colunas 2 e 3: Links Essenciais */}
                    <div className="col-span-1 md:col-span-2 grid grid-cols-2 gap-8">
                        <div>
                            <h3 className="font-semibold text-white mb-4">Institucional</h3>
                            <ul className="space-y-3">
                                <li><a href="/sobre" className="text-sm text-gray-400 hover:text-green-500 transition-colors">Sobre Nós</a></li>
                                <li><a href="/contato" className="text-sm text-gray-400 hover:text-green-500 transition-colors">Contato</a></li>
                                <li><a href="/blog" className="text-sm text-gray-400 hover:text-green-500 transition-colors">Blog</a></li>
                            </ul>
                        </div>
                        <div>
                            <h3 className="font-semibold text-white mb-4">Suporte</h3>
                            <ul className="space-y-3">
                                <li><a href="/ajuda" className="text-sm text-gray-400 hover:text-green-500 transition-colors">Central de Ajuda</a></li>
                                <li><a href="/termos" className="text-sm text-gray-400 hover:text-green-500 transition-colors">Termos de Serviço</a></li>
                                <li><a href="/privacidade" className="text-sm text-gray-400 hover:text-green-500 transition-colors">Política de Privacidade</a></li>
                            </ul>
                        </div>
                    </div>
                </div>

                {/* Barra Inferior */}
                <div className="flex flex-col sm:flex-row items-center justify-between py-6 border-t border-slate-800">
                    <p className="text-sm text-gray-500 mb-4 sm:mb-0 text-center sm:text-left">© {new Date().getFullYear()} foodflow. Todos os direitos reservados.</p>
                    <div className="flex space-x-5">
                        {socialLinks.map(social => {
                            const Icon = social.icon; // Componente do ícone
                            return (
                                <a key={social.name} href={social.href} className="text-gray-500 hover:text-white transition-colors">
                                    <Icon size={20} />
                                    <span className="sr-only">{social.name}</span>
                                </a>
                            )
                        })}
                    </div>
                </div>
            </div>
        </footer>
    );
}
